
import BScroll from 'better-scroll'
import LoadMore from 'vux/src/components/load-more/index.vue'

let count = 1
export default {
name: 'goodsManage',
components: {
LoadMore
},
data () {
return {
params: {
merchantId: '',
status: 1, //商品状态【1-已上架 2-未上架】
page: 1,
pageSize: 10
},
isMoreData: true,
data: [],
baseInfo: {
goodsDownNum: 0,
goodsOnNum: 0
},
pulldownMsg: '下拉刷新',
pullupMsg: '加载更多',
alertHook: 'none'
}
},
methods: {
addGoods() {
this.$router.push({
name: 'goodsAdd'
})
},
goDetail(id) {
this.$router.push({
name: 'goodsDetail',
query: {
tabIndex: this.params.status,
id: id
}
})
},
getBack() {
this.$router.go(-1)
},
getData() {
this.$http.fetchGet('/merchant/good/get/list', this.params).then((res)=> {
this.baseInfo.goodsDownNum = res.data.data.goodsDownNum
this.baseInfo.goodsOnNum = res.data.data.goodsOnNum
this.data = res.data.data.goodsList
if(this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(()=>{
if(this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}
this.initScroll()
})
},
refreshData() {
this.$refs.content.style.height = 'auto'
this.pullupMsg = '加载中。。。';
this.params.page = 1
this.$http.fetchGet('/merchant/good/get/list', this.params).then((res) => {
this.baseInfo.goodsDownNum = res.data.data.goodsDownNum
this.baseInfo.goodsOnNum = res.data.data.goodsOnNum
this.data = res.data.data.goodsList
if(this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(()=>{
if(this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}

this.pullupMsg = '加载更多';

this.scroll.refresh();
})
},
loadMoreData() {
if(this.data.length < this.params.pageSize) {
return
}
this.params.page++
this.$http.fetchGet('/merchant/good/get/list', this.params).then((res)=> {
this.baseInfo.goodsDownNum = res.data.data.goodsDownNum
this.baseInfo.goodsOnNum = res.data.data.goodsOnNum
if(this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
}
if(res.data.data.goodsList.length > 0) {
res.data.data.goodsList.map((item)=>{
this.data.push(item)
})
} else {
this.isMoreData = false
}

this.pulldownMsg = '下拉刷新'

this.scroll.refresh();
})
},
initScroll() {
this.$nextTick(()=> {
this.scroll = new BScroll(this.$refs.wrapper, {       //初始化better-scroll
probeType: 1,   //1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
click: true   
})

this.scroll.on('scroll', (pos)=> {
if(pos.y > 30){
this.pulldownMsg = '释放立即刷新'
}
});

this.scroll.on('touchEnd', (pos)=> {  //上拉刷新
if (pos.y > 30) {
this.refreshData()
} else if (pos.y < (this.scroll.maxScrollY - 30)) {   //下拉加载
this.loadMoreData()
}
})
})
},
changeTab(tabIndex) {
this.$refs.content.style.height = 'auto'
this.params.page = 1
this.params.status = tabIndex
this.refreshData()
},
refreshalert() {   //刷新成功提示
this.alertHook = 'block';
setTimeout(()=> {
this.alertHook = 'none'
}, 1000)
}
},
created() {
this.params.merchantId = this.$store.state.merchantId
this.getData()
}
}



